import {
  Button, Switch, TextareaAutosize, TextField,
} from '@mui/material';
import { observer } from 'mobx-react-lite';
import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import Card from '../../components/Card';
import MultiRow from '../../components/MultiRow';
import Note from '../../components/Notes';
import Page from '../../components/Page';
import SubTitle from '../../components/SubTitle';
import { formatDate } from '../../functions/formatting';
import useStore from '../../hooks/useStore';

const NotesPage = observer(() => {
  const { companyStore } = useStore();

  const [activeNotes, setActiveNotes] = useState(new Set());

  const onClick = (e: any) => {
    setActiveNotes((previousState) => new Set([...previousState, e.target.name]));
  };

  return (
    <Page>
      <Card>
        <SubTitle subTitle="Noter" />
        <Note isActive title="Redovisningsprinciper">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />
          <MultiRow isBig isBold isSum isNoBorder subTitle="Tjnste- och entreprenaduppdrag" />
          <MultiRow isBold isSum isNoBorder subTitle="Utfr fretaget tjnste- och/eller entreprenaduppdrag till fast pris?" current={<Switch />} />
          <MultiRow isBig isBold isSum isNoBorder subTitle="Egentillverkade varor" />
          <MultiRow isBold isSum isNoBorder subTitle="Tillverkar fretaget egna varor?" current={<Switch />} />
        </Note>
        <Note isActive title="Medelantal anstllda">
          <MultiRow subTitle="" current={`${formatDate(companyStore.company.fiscalYears.currentStart)} - ${formatDate(companyStore.company.fiscalYears.currentEnd)}`} />
          <MultiRow
            subTitle="Medelantal anstllda under ret"
            current={(
              <TextField label="Mste anges" />
)}
          />
        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('Stllda skerheter')} title="Stllda skerheter">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('Eventualfrpliktelser')} title="Eventualfrpliktelser">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('Exceptionella intkter och kostnder')} title="Exceptionella intkter och kostnder">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('Tillgngar, Avsttningar och Skulder som avser flera poster')} title="Tillgngar, Avsttningar och Skulder som avser flera poster">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('Ekonomiska arrangemang som inte redovisas i balansrkningen')} title="Ekonomiska arrangemang som inte redovisas i balansrkningen">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>
        <Note onClick={(e) => onClick(e)} isActive={activeNotes.has('vriga kommentarer')} title="vriga kommentarer">
          <MultiRow isBig isBold isSum isNoBorder subTitle="Regelverk" current={<TextareaAutosize cols={50} minRows={5} minLength={200} />} />

        </Note>

        <NavLink to="/year-story">
          <div style={{ display: 'grid', justifyContent: 'center', marginTop: 12 }}>
            <Button variant="contained">Fortstt</Button>
          </div>
        </NavLink>
      </Card>
    </Page>
  );
});
export default NotesPage;
